<template><section>
<h2>webSocket封装</h2>
<pre v-pre=""><code class="hljs language-javascript">  <span class="hljs-keyword">import</span> { SocketApi } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  Vue.use(SocketApi) <span class="hljs-comment">// 这边因为WebSocket是原生名称 怕起冲突改为SocketApi</span>
  <span class="hljs-comment">// 访问 this.$webSocket</span>
</code></pre>
<h4>connectSocket 开启一个连接</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickConnectSocket\&quot;&gt;点击开启一个连接&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * @param {*} { url, succ, fail }\n     * @param {是开发者服务器接口地址，必须是 wss 协议，且域名必须是后台配置的合法域名} { url }\n     * @param {否接口调用成功的回调函数} { succ }\n     * @param {否接口调用失败的回调函数} { fail }\n     * @returns\n     */\n    onClickConnectSocket () {\n      this.$webSocket.connectSocket ({ url: this.$env.apiH5Wss + this.$request.token, succ(){}, fail(){} })\n    },\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickConnectSocket">点击开启一个连接</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickConnectSocket"</span>&gt;</span>点击开启一个连接<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * @param {*} { url, succ, fail }
     * @param {是开发者服务器接口地址，必须是 wss 协议，且域名必须是后台配置的合法域名} { url }
     * @param {否接口调用成功的回调函数} { succ }
     * @param {否接口调用失败的回调函数} { fail }
     * @returns
     */</span>
    onClickConnectSocket () {
      <span class="hljs-keyword">this</span>.$webSocket.connectSocket ({ <span class="hljs-attr">url</span>: <span class="hljs-keyword">this</span>.$env.apiH5Wss + <span class="hljs-keyword">this</span>.$request.token, succ(){}, fail(){} })
    },
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>其他小方法不在赘述自行查看</h4>
<pre v-pre=""><code class="hljs language-javascript"><span class="hljs-comment">/**
 * 通过 WebSocket 连接发送数据
 */</span>
sendSocketMessage ({data}) {}
<span class="hljs-comment">/**
 * 关闭 WebSocket 连接。
 */</span>
closeSocket () {}
<span class="hljs-comment">/**
 * 监听WebSocket连接打开事件。
 */</span>
onSocketOpen (callback) {}
<span class="hljs-comment">/**
 * 监听WebSocket接受到服务器的消息事件。
 */</span>
onSocketMessage (callback) {}
<span class="hljs-comment">/**
 * 监听WebSocket关闭
 */</span>
onSocketClose (callback) {}
<span class="hljs-comment">/**
 * 监听WebSocket错误。
 */</span>
onSocketError (callback) {}
</code></pre>
<h4>websocketInit 初始化一个websocket</h4>
<p>默认之存在一个websocket链接</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWebsocketInit\&quot;&gt;初始化一个websocket&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickWebsocketInit() {\n      this.$webSocket.websocketInit()\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWebsocketInit">初始化一个websocket</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWebsocketInit"</span>&gt;</span>初始化一个websocket<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickWebsocketInit() {
      <span class="hljs-keyword">this</span>.$webSocket.websocketInit()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>这边这两个是添加和删除websocket监听回调 也就是消息接收后的回调</h4>
<pre v-pre=""><code class="hljs language-javascript">  websocketRegisterHandler (msgtype, handler) { <span class="hljs-comment">//msgtype事件类型 handler事件接收回调</span>
  }
  websocketUnregisterHandler (msgtype) { <span class="hljs-comment">// 删除当前的所以回调监听</span>
  }
</code></pre>
<h4>websocketExit 退出连接</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWebsocketExit\&quot;&gt;退出连接&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickWebsocketExit() {\n      this.$webSocket.websocketExit()\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWebsocketExit">退出连接</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWebsocketExit"</span>&gt;</span>退出连接<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickWebsocketExit() {
      <span class="hljs-keyword">this</span>.$webSocket.websocketExit()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>websocketSendMessage 发送消息</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWebsocketSendMessage\&quot;&gt;发送消息&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onClickWebsocketSendMessage() {\n      //msgtype 发送消息类型 msgdata 发送内容 msgFrom 发送给什么平台\n      this.$webSocket.websocketSendMessage({msgtype:'xx', msgdata: {xx:'xx'}, msgFrom: 'miniprogram'})\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWebsocketSendMessage">发送消息</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWebsocketSendMessage"</span>&gt;</span>发送消息<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onClickWebsocketSendMessage() {
      <span class="hljs-comment">//msgtype 发送消息类型 msgdata 发送内容 msgFrom 发送给什么平台</span>
      <span class="hljs-keyword">this</span>.$webSocket.websocketSendMessage({<span class="hljs-attr">msgtype</span>:<span class="hljs-string">'xx'</span>, <span class="hljs-attr">msgdata</span>: {<span class="hljs-attr">xx</span>:<span class="hljs-string">'xx'</span>}, <span class="hljs-attr">msgFrom</span>: <span class="hljs-string">'miniprogram'</span>})
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  methods: {
    onClickConnectSocket () {
      this.$webSocket.connectSocket ({ url: this.$env.apiH5Wss + this.$request.token, succ(){}, fail(){} })
    },
    onClickWebsocketInit() {
      this.$webSocket.websocketInit()
    },
    onClickWebsocketExit() {
      this.$webSocket.websocketExit()
    },
    onClickWebsocketSendMessage() {
      this.$webSocket.websocketSendMessage({msgtype:'xx', msgdata: {xx:'xx'}, msgFrom: 'miniprogram'})
    }
  }
}
</script>


